const sWidth: number = window.innerWidth;

const SocialScreen: React.FC<any> = (props) => {
    const [loaded, setLoaded] = React.useState<boolean>(false);
    const seo: SeoSettingsType = {
        title: "İletişim | Can Gökçeaslan",
        description: "Bu sayfada bana ait tüm profil linklerine ulaşabilirsiniz.",
        keywords: "cangokceaslan, Can Gökçeaslan, Can Gokceaslan, CanGokceaslan, Node.js, NodeJs, JavaScript, Fullstack Developer, CoFounder, Girişim",
        author: "Can Gökçeaslan",
        locale: "tr",
        siteName: "https://www.cangokceaslan.com/social"
    }
    const renderSocialItem = (config: {
        className: string,
        logoText: string,
        icon?: any,
        iconImage?: any,
        link: string,
        name: string,
        description: string
    }) => {
        return (
            <div className="item" onClick={() => {
                window.open(config?.link, "__blank")
            }}>
                <div className="social-item">
                    <div className={`logo-container ${config?.className}`}>
                        <div style={{
                            display: "flex",
                            alignItems: "center",
                            justifyContent: "center"
                        }}>
                            {config?.icon && <config.icon size={30} color="white" style={{
                                marginRight: "8px"
                            }} />
                            }
                            {config.iconImage && <img src={config.iconImage} width="30px" height="30px" alt={config?.logoText} />}
                            <div style={{
                                fontSize: "20px",
                                fontWeight: "bold",
                                fontFamily: "Cairo",
                                color: "white"
                            }}>{config?.logoText}</div>
                        </div>
                        <div className="profilePhotoContainer">
                            <img src={MainImage} width="100px" height="100px" alt="Can Gökçeaslan'ın ana resimi" />
                        </div>
                    </div>
                    <div className="name">
                        {config?.name}
                    </div>
                    <div className="description">
                        {config?.description}
                    </div>
                </div>
            </div>
        )
    }

    useEffect(() => {
        setLoaded(true);
    }, [])
    if (!loaded) return <div className="loading">Yükleniyor</div>
    return (
        <React.Fragment>
            <DocumentSettings docData={seo} />
            <h1 style={{ display: "none" }}>İletişim</h1>
            <div className="social" style={{
                paddingTop: "40px",
                marginBottom: "20px",
                ...(sWidth < 992) && {
                    marginTop: "-40px"
                }
            }} >
                <div className="container">
                    <div className="row">
                        {data.map((config, i) => renderSocialItem(config))}
                    </div>
                </div>
            </div>
            <SingleCourse />
        </React.Fragment>
    )
}

